"use client";

import { useUserStore } from "@/hooks/user";
import { ENDPOINTS } from "@/shared/site";
import { getError, postRequest } from "@/utils/axios-instance";
import { Button } from "@heroui/button";
import { Input } from "@heroui/input";
import { useRouter, useSearchParams } from "next/navigation";
import {
  FocusEvent,
  FormEvent,
  MouseEvent,
  useCallback,
  useState,
} from "react";
import { FaEye, FaEyeSlash } from "react-icons/fa";

export default function SignIn() {
  const [isHidden, setIsHidden] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState({ email: "", password: "", general: "" });
  const { setUser } = useUserStore();

  const { push } = useRouter();
  const searchParams = useSearchParams();
  const next = searchParams.get("next");

  const handleHidden = useCallback(
    (event: MouseEvent<HTMLButtonElement>) => {
      event.preventDefault();
      setIsHidden((prev) => !prev);
    },
    [isHidden]
  );

  const handleFocus = useCallback(
    (event: FocusEvent<HTMLInputElement>) => {
      const { name } = event.currentTarget;
      setError((prev) => ({
        ...prev,
        [name]: "",
        general: "",
      }));
    },
    [error]
  );

  const handleSubmit = useCallback(
    async (event: FormEvent<HTMLFormElement>) => {
      event.preventDefault();
      setError({ email: "", password: "", general: "" });

      const form = event.currentTarget;
      const email = form.email.value;
      const password = form.password.value;

      let is_valid = true;

      if (email === "") {
        setError((prev) => ({
          ...prev,
          email: content.errors.regular.form.email.required.value,
        }));
        is_valid = false;
      }
      if (password === "") {
        setError((prev) => ({
          ...prev,
          password: "This field is required.",
        }));
        is_valid = false;
      }

      if (!is_valid) return;

      setIsLoading(true);

      try {
        const { data } = await postRequest({
          url: ENDPOINTS.sign_in,
          data: { email, password },
        });
        if (data) {
          setUser(data);
          push(next != null && next !== "/" ? next : ROUTES.main);
        }
      } catch (err) {
        const { data, status } = getError(err as AxiosErrorProps);
        if (status && status >= 500) {
          setAlert((prev) => ({
            ...prev,
            isVisible: true,
            color: "danger",
            description: content.errors.server.value,
          }));
        } else {
          setError((prev) => ({
            ...prev,
            general: data?.message,
          }));
        }
        setIsLoading(false);
      }
    },
    [error, isLoading]
  );

  return (
    <div className="bg-[url(/media/images/sign-in/banner.jpg)] h-svh">
      <div className="absolute inset-0 flex items-center justify-center bg-primary/90">
        <form
          className="max-w-100 w-full bg-white px-8 pt-12 pb-16 rounded-lg"
          noValidate
          onSubmit={handleSubmit}
        >
          <h1 className="text-center font-semibold text-xl">Welcome Back !</h1>
          <h2 className="text-center text-gray-400 text-sm mb-8 mt-2">
            Sign in to continue to Xazna AI
          </h2>
          <Input
            onFocus={handleFocus}
            isInvalid={Boolean(error.email) || Boolean(error.general)}
            errorMessage={error.email}
            classNames={{
              base: Boolean(error.email) ? "mb-0" : "mb-6",
              inputWrapper:
                "dark:bg-neutral-900 border-1 border-gray-200 rounded-sm shadow-none",
              label: "text-default-500",
            }}
            color="primary"
            variant="bordered"
            placeholder={"Email"}
            type="text"
            name="email"
          />
          <div className="relative">
            <Input
              onFocus={handleFocus}
              isInvalid={Boolean(error.password) || Boolean(error.general)}
              errorMessage={
                Boolean(error.password) ? error.password : error.general
              }
              classNames={{
                base: "relative mb-2",
                inputWrapper:
                  "dark:bg-neutral-900 border-1 border-gray-200 rounded-sm shadow-none",
                input: "pr-8",
                label: "text-default-500",
                helperWrapper: "absolute top-full",
              }}
              color="primary"
              variant="bordered"
              isDisabled={isLoading}
              placeholder={"Password"}
              name="password"
              type={isHidden ? "password" : "text"}
            />
            <div className="h-full flex items-center absolute inset-y-0 right-3">
              <button className="cursor-pointer" onClick={handleHidden}>
                {isHidden ? (
                  <FaEyeSlash className="text-default-500 w-4 h-4" />
                ) : (
                  <FaEye className="text-default-500 w-4 h-4" />
                )}
              </button>
            </div>
          </div>
          <Button fullWidth color="primary" className="rounded-sm mt-12">
            Sign In
          </Button>
        </form>
      </div>
    </div>
  );
}
